//http
import axios from 'axios';

//
const REQROOTPATH = 'http://localhost:7080/server/';

const Ajax = axios.create({
	baseURL: REQROOTPATH,
	timeout: 5000,
	// headers: {
	// 	'Content-Type': 'application/x-www-form-urlencoded',
	// }
});

const OUTPUTDEBUG = false;


//
Ajax.interceptors.request.use(function (config) {
	if(OUTPUTDEBUG){
		console.log('---> ' ,config);
	}
	//
	return config;
}, function (err) {
	//Do something with request error
	return Promise.reject(error);
});


//
Ajax.interceptors.response.use(function (res) {
	if(OUTPUTDEBUG){
		console.log('---> ' ,res);
	}
	//
	return res;
}, function (err) {
	//Do something with response error
	return Promise.reject(error);
})

export default {
	/**
	 * 
     * @example
     * 
     * api.getMap()
     * .then(()=>{
     *      //
     * })
     * .catch(()=>{
     *      //
     * })
	 */
	getMap(){
		return Ajax.get('getMap.action');
	},
	/**
	 * POST
	 * 
	 * @param {any} json 
     * 
     * @example
     * 
     * api.testSave({formJson: ''})
     * .then(()=>{
     *      //
     * })
     * .catch(()=>{
     *      //
     * })
	 */
	saveTest(json){
		return Ajax.post('save.action' ,{
			formJson: json
		})
	},
	/**
	 * 
	 * @example
	 * api.getAll(api.getMap(),testSave({formJson: ''}))
	 * .then((res)=>{
	 *    res[0]		getMap
	 *    res[1]		testSave
	 * })
	 * 
	 */
	getAll(...params){
		return axios.all(params);
	}
}


